import React from 'react'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'
import BookmarksIcon from '@material-ui/icons/Bookmarks'
import InvertColorsIcon from '@material-ui/icons/InvertColors'
import { IconButton } from '@material-ui/core'
import { APP_ACTIONS } from "../App"

const useStyles = makeStyles(theme => ({
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'flex',
    flexDirection: "column",
    alignItems: "flex-start",
    textTransform: "uppercase"
  },
  appBar: {
    position: "fixed",
    zIndex: 10000,
    top: theme.spacing(1),
    width: `calc(100% - ${theme.spacing(2)}px)`,
    left: theme.spacing(1),
    borderRadius: theme.spacing(1)
  }
}));

const Header = ({ appDispatch, ...props }) => {
  const classes = useStyles()

  return (
    <AppBar className={classes.appBar}>
      <Toolbar>
        <BookmarksIcon style={{marginRight: 8}}/>
        <div className={classes.title}>
          <Typography variant="h5">
            {props.title}
          </Typography>
        </div>
        <IconButton style={{padding:0}} 
          onClick={() => appDispatch({
            type: APP_ACTIONS.TOGGLE_THEME,
            payload: { value: !(JSON.parse(localStorage.getItem("theme")).value) }
          })}>
          <InvertColorsIcon style={{color: "#fff"}}/>
        </IconButton>
        
      </Toolbar>
    </AppBar>
  );
}

export default Header;